.profile-create {
  max-width: 39.5rem;
  margin: 0 auto;
}

.profile-create__header {
  border-bottom: 0.2rem solid var(--white-v7);
  margin-bottom: 2.5rem;
  padding-bottom: 3rem;

  @media screen and (min-width: $screen-lg) {
    margin-bottom: 3rem;
    padding-bottom: 4rem;
  }
}

.profile-create__heading {
  @include heading-playfair-mobile;
  @include left-accent-line-text;
  border-color: var(--pink);

  @media screen and (min-width: $screen-lg) {
    @include heading-playfair;
  }
}

.profile-create__profile-info {
  border-bottom: 0.2rem solid var(--white-v7);
  margin-bottom: 3rem;
  padding-bottom: 1rem;

  @media screen and (min-width: $screen-lg) {
    margin-bottom: 4.5rem;
    padding-bottom: 1.5rem;
  }
}

.profile-create__member-agreement {
  margin-top: 5rem;

  .form-check-input {
    margin-left: -1.6rem;
  }

  label {
    color: var(--white);

    a {
      text-decoration: underline;
    }
  }

  .form__error {
    margin-left: -1.5rem;
  }
}
